.Toolbar{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align:center;
        align-items:center;
    padding: 0 30px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    background-color: rgb(30,85,92);
